/* Toastmasters International Theme Variables */
:root {
  /* 头马官方主色调 */
  --tm-true-maroon: #772432; /* 真酒红色 - Pantone 188 */
  --tm-loyal-blue: #004165; /* 忠诚蓝 - Pantone 302 */
  --tm-dark-blue: #01293e;
  --tm-cool-gray: #a9b2b1; /* 冷灰色 - Pantone 442 */
  --tm-happy-yellow: #f2df74; /* 快乐黄 - Pantone 127 */

  /* 头马渐变色 */
  --tm-blue-gradient-start: #004165;
  --tm-blue-gradient-end: #006094;
  --tm-maroon-gradient-start: #772432;
  --tm-maroon-gradient-end: #9a2f3f;

  /* 语义化颜色 */
  --tm-primary: var(--tm-loyal-blue);
  --tm-secondary: var(--tm-true-maroon);
  --tm-accent: var(--tm-happy-yellow);
  --tm-neutral: var(--tm-cool-gray);

  /* 文本颜色 */
  --tm-text-primary: #ffffff;
  --tm-text-secondary: var(--tm-cool-gray);
  --tm-text-dark: #2c3e50;
  --tm-text-light: #ffffff;

  /* 背景色 */
  --tm-bg-primary: var(--tm-loyal-blue);
  --tm-bg-secondary: var(--tm-true-maroon);
  --tm-bg-light: #f8f9fa;
  --tm-bg-dark: #1a1a1a;

  /* 边框和分割线 */
  --tm-border-color: var(--tm-cool-gray);
  --tm-border-light: rgba(169, 178, 177, 0.3);
  --tm-border-dark: rgba(119, 36, 50, 0.3);

  /* 状态颜色 */
  --tm-success: #52c41a;
  --tm-warning: var(--tm-happy-yellow);
  --tm-error: #ff4d4f;
  --tm-info: var(--tm-loyal-blue);

  /* 阴影 */
  --tm-shadow-sm: 0 2px 4px rgba(119, 36, 50, 0.1);
  --tm-shadow-md: 0 4px 8px rgba(119, 36, 50, 0.15);
  --tm-shadow-lg: 0 8px 16px rgba(119, 36, 50, 0.2);

  /* 头马品牌渐变 */
  --tm-gradient-primary: linear-gradient(
    135deg,
    var(--tm-loyal-blue) 0%,
    var(--tm-blue-gradient-end) 100%
  );
  --tm-gradient-secondary: linear-gradient(
    135deg,
    var(--tm-true-maroon) 0%,
    var(--tm-maroon-gradient-end) 100%
  );
  --tm-gradient-accent: linear-gradient(
    135deg,
    var(--tm-happy-yellow) 0%,
    #f0d85a 100%
  );
}

/* 覆盖 Ant Design 的主题变量 */
.ant-layout {
  background: var(--tm-bg-light);
}

.ant-layout-sider {
  background: var(--tm-bg-primary) !important;
}

.ant-layout-header {
  background: #ffffff !important;
  border-bottom: 1px solid var(--tm-border-light);
}

.ant-menu-dark {
  background: var(--tm-bg-primary) !important;
}

.ant-menu-dark .ant-menu-item {
  color: var(--tm-text-primary) !important;
}

.ant-menu-dark .ant-menu-item:hover {
  background: var(--tm-true-maroon) !important;
  color: var(--tm-text-primary) !important;
}

.ant-menu-dark .ant-menu-item-selected {
  background: var(--tm-true-maroon) !important;
  color: var(--tm-text-primary) !important;
}

.ant-menu-dark .ant-menu-submenu-title {
  color: var(--tm-text-primary) !important;
}

.ant-menu-dark .ant-menu-submenu-title:hover {
  color: var(--tm-text-primary) !important;
  background: var(--tm-true-maroon) !important;
}

.ant-menu-dark .ant-menu-submenu-open .ant-menu-submenu-title {
  color: var(--tm-text-primary) !important;
  background: transparent !important;
}

/* 二级菜单样式 */
.ant-menu-dark .ant-menu-submenu .ant-menu {
  background: var(--tm-dark-blue) !important;
}

.ant-menu-dark .ant-menu-submenu .ant-menu-item {
  color: rgba(255, 255, 255, 0.85) !important;
  padding-left: 48px !important;
  background: var(--tm-dark-blue) !important;
}

.ant-menu-dark .ant-menu-submenu .ant-menu-item:hover {
  background: var(--tm-true-maroon) !important;
  color: var(--tm-text-primary) !important;
}

.ant-menu-dark .ant-menu-submenu .ant-menu-item-selected {
  background: var(--tm-true-maroon) !important;
  color: var(--tm-text-primary) !important;
}

/* 三级菜单样式 */
.ant-menu-dark .ant-menu-submenu .ant-menu-submenu .ant-menu-item {
  padding-left: 64px !important;
}

/* 菜单展开动画优化 */
.ant-menu-dark .ant-menu-submenu-inline > .ant-menu {
  background: var(--tm-dark-blue) !important;
}

/* 按钮主题 */
.ant-btn-primary {
  background: var(--tm-gradient-primary) !important;
  border: none !important;
  box-shadow: var(--tm-shadow-sm);
}

.ant-btn-primary:hover {
  background: var(--tm-secondary) !important;
  transform: translateY(-1px);
  box-shadow: var(--tm-shadow-md);
}

.ant-btn-default {
  border-color: var(--tm-border-color) !important;
  color: var(--tm-primary) !important;
}

.ant-btn-default:hover {
  border-color: var(--tm-primary) !important;
  color: var(--tm-primary) !important;
}

/* 卡片主题 */
.ant-card {
  border: 1px solid var(--tm-border-light);
  box-shadow: var(--tm-shadow-sm);
}

.ant-card-head {
  background: var(--tm-gradient-primary);
  color: var(--tm-text-primary);
  border-bottom: none;
}

.ant-card-head-title {
  color: var(--tm-text-primary) !important;
}

/* 表格主题 */
.ant-table-thead > tr > th {
  background: var(--tm-bg-light) !important;
  color: var(--tm-text-dark) !important;
  border-bottom: 2px solid var(--tm-primary) !important;
}

.ant-table-tbody > tr:hover > td {
  background: rgba(0, 65, 101, 0.05) !important;
}

.ant-table-tbody > tr.ant-table-row-selected > td {
  background: rgba(0, 65, 101, 0.1) !important;
}

/* 分页主题 */
.ant-pagination-item-active {
  background: var(--tm-primary) !important;
  border-color: var(--tm-primary) !important;
}

.ant-pagination-item-active a {
  color: var(--tm-text-primary) !important;
}

/* 表单主题 */
.ant-input:focus,
.ant-input-focused {
  border-color: var(--tm-primary) !important;
  box-shadow: 0 0 0 2px rgba(0, 65, 101, 0.2) !important;
}

.ant-select:not(.ant-select-disabled):hover .ant-select-selector {
  border-color: var(--tm-primary) !important;
}

.ant-select-focused .ant-select-selector {
  border-color: var(--tm-primary) !important;
  box-shadow: 0 0 0 2px rgba(0, 65, 101, 0.2) !important;
}

/* 模态框主题 */
.ant-modal-header {
  background: var(--tm-gradient-primary);
  border-bottom: none;
}

.ant-modal-title {
  color: var(--tm-text-primary) !important;
}

.ant-modal-close {
  color: var(--tm-text-primary) !important;
}

.ant-modal-close:hover {
  color: var(--tm-happy-yellow) !important;
}

/* 消息提示主题 */
.ant-message-success .anticon {
  color: var(--tm-success) !important;
}

.ant-message-error .anticon {
  color: var(--tm-error) !important;
}

.ant-message-warning .anticon {
  color: var(--tm-warning) !important;
}

.ant-message-info .anticon {
  color: var(--tm-info) !important;
}

/* 标签主题 */
.ant-tag {
  border-color: var(--tm-border-color);
}

.ant-tag-blue {
  background: rgba(0, 65, 101, 0.1);
  border-color: var(--tm-primary);
  color: var(--tm-primary);
}

.ant-tag-red {
  background: rgba(119, 36, 50, 0.1);
  border-color: var(--tm-secondary);
  color: var(--tm-secondary);
}

.ant-tag-yellow {
  background: rgba(242, 223, 116, 0.2);
  border-color: var(--tm-accent);
  color: #8b7300;
}

/* 进度条主题 */
.ant-progress-bg {
  background: var(--tm-gradient-primary) !important;
}

/* 开关主题 */
.ant-switch-checked {
  background: var(--tm-primary) !important;
}

/* 滑块主题 */
.ant-slider-track {
  background: var(--tm-primary) !important;
}

.ant-slider-handle {
  border-color: var(--tm-primary) !important;
}

/* 头马品牌专用类 */
.tm-gradient-bg {
  background: var(--tm-gradient-primary);
  color: var(--tm-text-primary);
}

.tm-gradient-secondary-bg {
  background: var(--tm-gradient-secondary);
  color: var(--tm-text-primary);
}

.tm-accent-bg {
  background: var(--tm-accent);
  color: var(--tm-text-dark);
}

.tm-text-primary {
  color: var(--tm-primary) !important;
}

.tm-text-secondary {
  color: var(--tm-secondary) !important;
}

.tm-text-accent {
  color: var(--tm-accent) !important;
}

.tm-border-primary {
  border-color: var(--tm-primary) !important;
}

.tm-border-secondary {
  border-color: var(--tm-secondary) !important;
}

/* 头马品牌卡片样式 */
.tm-brand-card {
  border: 2px solid var(--tm-primary);
  border-radius: 8px;
  box-shadow: var(--tm-shadow-md);
  transition: all 0.3s ease;
}

.tm-brand-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--tm-shadow-lg);
}

.tm-brand-card .ant-card-head {
  background: var(--tm-gradient-primary);
  border-radius: 6px 6px 0 0;
}

/* 头马品牌按钮样式 */
.tm-btn-secondary {
  background: var(--tm-gradient-secondary) !important;
  border: none !important;
  color: var(--tm-text-primary) !important;
  box-shadow: var(--tm-shadow-sm);
}

.tm-btn-secondary:hover {
  background: var(--tm-primary) !important;
  color: var(--tm-text-primary) !important;
  transform: translateY(-1px);
  box-shadow: var(--tm-shadow-md);
}

.tm-btn-accent {
  background: var(--tm-accent) !important;
  border: none !important;
  color: var(--tm-text-dark) !important;
  box-shadow: var(--tm-shadow-sm);
}

.tm-btn-accent:hover {
  background: #f0d85a !important;
  color: var(--tm-text-dark) !important;
  transform: translateY(-1px);
  box-shadow: var(--tm-shadow-md);
}

/* 响应式调整 */
@media (max-width: 768px) {
  .ant-layout-sider {
    background: var(--tm-bg-primary) !important;
  }

  .ant-drawer .ant-drawer-header {
    background: var(--tm-gradient-primary);
  }

  .ant-drawer .ant-drawer-title {
    color: var(--tm-text-primary) !important;
  }
}
